#div {
    animation: div 4000ms ease-in 500ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }

  @keyframes div {
    0% {
      transform: translateY(100px);
      opacity: 0;
    }
  
    100% {
      transform: translateY(0px);
      opacity: 1;
    }
  }
  .project-card{
    box-shadow: #38B6FF 0px 10px 20px, #38B6FF 0px 6px 6px;
  }