.contact-github {
    height: 40px;
    width: 40px;
    border-radius: 50%;
  
    animation: social-media 1000ms ease-in 2000ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }
  .contact-linkedin {
    height: 40px;
    width: 40px;
    border-radius: 50%;
 
    animation: social-media 2000ms ease-in 2000ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }
  .mail {
    height: 40px;
    width: 40px;
    border-radius: 50%;
 
    animation: social-media 3000ms ease-in 2000ms;
    animation-fill-mode: forwards;
    transform: translateY(-100px);
    opacity: 0;
  }
  .contact-github:hover{
    box-shadow: #38B6FF 0px 14px 28px, #38B6FF 0px 10px 10px;
    }
    .mail:hover{
    box-shadow:#38B6FF 0px 14px 28px, #38B6FF 0px 10px 10px ;
    }
    .contact-linkedin:hover{
    box-shadow: #38B6FF 0px 14px 28px, #38B6FF 0px 10px 10px;
    }
  
  @keyframes social-media {
      0% {
        transform: translateX(500px);
        opacity: 0;
      }
    
      100% {
        transform: translateX(0px);
        opacity: 1;
      }
    }

  