.title {
  animation: text-load 2000ms ease-in 2000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}

.button {
  animation: text-load 2000ms ease-in 500ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.button:hover{
  box-shadow:#38B6FF 0px 14px 28px,#38B6FF 0px 10px 10px;
}
.name {
  animation: text-load 2000ms ease-in 1500ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
