.text {
  animation: text-load 2000ms ease-in 1000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}

@keyframes text-load {
  0% {
    transform: translateY(-300px);
    opacity: 0;
  }

  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

