.git {
  height: 50px;
  width: 50px;
  border-radius: 50%;

  animation: social-media 2000ms ease-in 2000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.in {
  height: 50px;
  width: 50px;
  border-radius: 50%;
 
  animation: social-media 3000ms ease-in 2000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.twit {
  height: 50px;
  width: 50px;
  border-radius: 50%;

  animation: social-media 4000ms ease-in 2000ms;
  animation-fill-mode: forwards;
  transform: translateY(-100px);
  opacity: 0;
}
.git:hover{
box-shadow:#38B6FF 0px 14px 28px,#38B6FF 0px 10px 10px;
}
.twit:hover{
box-shadow:#38B6FF 0px 14px 28px,#38B6FF 0px 10px 10px;
}
.in:hover{
box-shadow:#38B6FF 0px 14px 28px,#38B6FF 0px 10px 10px;
}
@keyframes social-media {
    0% {
      transform: translateX(500px);
      opacity: 0;
    }
  
    100% {
      transform: translateX(0px);
      opacity: 1;
    }
  }

